<template>
  <div class="art-tabs-container">
    <div class="art-tabs-bar">
      <span v-for="(tab, idx) in tabs" :key="tab" :class="['art-tab', { active: activeTab === idx }]" @click="selectTab(idx)">
        {{ tab }}
      </span>
    </div>
    <div class="art-main-carousel">
      <button class="arrow left" @click="prevImage"><span>&lt;</span></button>
      <img :src="currentImage" class="main-image" />
      <button class="arrow right" @click="nextImage"><span>&gt;</span></button>
    </div>
    <div class="art-thumbnails">
      <img v-for="(img, i) in images[activeTab]" :key="img" :src="img" :class="['thumb', { selected: i === activeIndex }]" @click="selectImage(i)" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'ArtTabs',
  data() {
    return {
      tabs: [
        'Culture',
        'Landscapes & Cityscapes',
        'Modern',
        'Panorama',
        'Portraits'
      ],
      images: [
        [
          'https://picsum.photos/id/1018/400/600',
          'https://picsum.photos/id/1015/400/600',
          'https://picsum.photos/id/1016/400/600',
          'https://picsum.photos/id/1020/400/600',
          'https://picsum.photos/id/1021/400/600',
        ],
        [
          'https://picsum.photos/id/1022/400/600',
          'https://picsum.photos/id/1023/400/600',
          'https://picsum.photos/id/1024/400/600',
          'https://picsum.photos/id/1025/400/600',
          'https://picsum.photos/id/1026/400/600',
        ],
        [
          'https://picsum.photos/id/1027/400/600',
          'https://picsum.photos/id/1028/400/600',
          'https://picsum.photos/id/1029/400/600',
          'https://picsum.photos/id/1030/400/600',
          'https://picsum.photos/id/1031/400/600',
        ],
        [
          'https://picsum.photos/id/1032/400/600',
          'https://picsum.photos/id/1033/400/600',
          'https://picsum.photos/id/1034/400/600',
          'https://picsum.photos/id/1035/400/600',
          'https://picsum.photos/id/1036/400/600',
        ],
        [
          'https://picsum.photos/id/1037/400/600',
          'https://picsum.photos/id/1038/400/600',
          'https://picsum.photos/id/1039/400/600',
          'https://picsum.photos/id/1040/400/600',
          'https://picsum.photos/id/1041/400/600',
        ]
      ],
      activeTab: 1,
      activeIndex: 0
    };
  },
  computed: {
    currentImage() {
      return this.images[this.activeTab][this.activeIndex];
    }
  },
  methods: {
    selectTab(idx) {
      this.activeTab = idx;
      this.activeIndex = 0;
    },
    selectImage(i) {
      this.activeIndex = i;
    },
    prevImage() {
      if (this.activeIndex > 0) {
        this.activeIndex--;
      } else {
        this.activeIndex = this.images[this.activeTab].length - 1;
      }
    },
    nextImage() {
      if (this.activeIndex < this.images[this.activeTab].length - 1) {
        this.activeIndex++;
      } else {
        this.activeIndex = 0;
      }
    }
  }
};
</script>

<style scoped>
/* 主容器 */
.art-tabs-container {
  background: #111;
  padding: 32px 0 0 0;
  min-height: 520px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
/* 顶部标签栏 */
.art-tabs-bar {
  display: flex;
  justify-content: center;
  gap: 24px;
  margin-bottom: 18px;
  font-size: 1.05rem;
  color: #bbb;
}
.art-tab {
  cursor: pointer;
  padding: 0 8px;
  font-weight: 400;
  transition: color 0.2s;
}
.art-tab.active {
  color: #fff;
  font-weight: 700;
  border-bottom: 2px solid #fff;
}
/* 主图轮播区 */
.art-main-carousel {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin-bottom: 18px;
}
.main-image {
  width: 320px;
  height: 440px;
  object-fit: cover;
  border-radius: 8px;
  box-shadow: 0 8px 32px rgba(0,0,0,0.35);
  background: #111;
  display: block;
}
.arrow {
  background: none;
  border: none;
  color: #fff;
  font-size: 2.2rem;
  cursor: pointer;
  padding: 0 18px;
  transition: color 0.2s;
  user-select: none;
}
.arrow:hover {
  color: #ffd700;
}
.arrow.left {
  margin-right: 8px;
}
.arrow.right {
  margin-left: 8px;
}
/* 缩略图预览区 */
.art-thumbnails {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 10px;
}
.thumb {
  width: 64px;
  height: 44px;
  object-fit: cover;
  border-radius: 4px;
  cursor: pointer;
  opacity: 0.7;
  border: 2px solid transparent;
  transition: border 0.2s, opacity 0.2s;
}
.thumb.selected {
  opacity: 1;
  border: 2px solid #fff;
}
</style>
