<template>
  <div class="home-container">
    <!-- 照片墙背景 -->
    <div class="photo-wall">
      <div 
        v-for="(photo, index) in photos" 
        :key="index" 
        class="photo-item"
        :style="{
          top: photo.top + '%',
          left: photo.left + '%',
          width: photo.size + 'px',
          height: photo.size + 'px',
          opacity: photo.visible ? photo.opacity : 0,
          transform: `rotate(${photo.rotate}deg)`,
          animationDelay: photo.delay + 's',
          transition: 'opacity 1s ease-in-out'
        }"
      >
        <img :src="photo.url" alt="Photo" />
      </div>
    </div>
    
    <!-- 主内容 -->
    <div class="content">
      <h1 class="typewriter">{{ displayedText }}<span class="cursor">|</span></h1>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      displayedText: '',
      fullText: 'ZIYING LV',
      typeSpeed: 150,
      eraseSpeed: 100,
      pauseTime: 2000,
      isTyping: true,
      photos: []
    };
  },
  mounted() {
    this.initPhotoWall();
    this.startTypewriter();
  },
  methods: {
    initPhotoWall() {
      // 生成100张随机照片
      const photos = [];
      for (let i = 0; i < 100; i++) {
        photos.push({
          url: `https://picsum.photos/200/200?random=${i}`,
          top: Math.random() * 100,
          left: Math.random() * 100,
          size: Math.random() * 100 + 50, // 50-150px
          opacity: Math.random() * 0.3 + 0.1, // 0.1-0.4
          rotate: Math.random() * 20 - 10, // -10到10度
          delay: Math.random() * 5, // 0-5秒延迟
          visible: true
        });
      }
      this.photos = photos;
      this.startPhotoAnimation();
    },
    
    startPhotoAnimation() {
      setInterval(() => {
        // 随机选择一些照片消失
        this.photos.forEach(photo => {
          if (Math.random() < 0.02) { // 2%的概率切换可见性
            photo.visible = !photo.visible;
          }
        });
      }, 1000); // 每秒检查一次
    },
    startTypewriter() {
      let text = '';
      let charIndex = 0;
      
      const type = () => {
        if (charIndex < this.fullText.length) {
          text += this.fullText.charAt(charIndex);
          this.displayedText = text;
          charIndex++;
          setTimeout(type, this.typeSpeed);
        } else {
          setTimeout(this.erase, this.pauseTime);
        }
      };
      
      type();
    },
    erase() {
      let text = this.displayedText;
      
      const eraseChar = () => {
        if (text.length > 0) {
          text = text.substring(0, text.length - 1);
          this.displayedText = text;
          setTimeout(eraseChar, this.eraseSpeed);
        } else {
          setTimeout(this.startTypewriter, 500);
        }
      };
      
      eraseChar();
    }
  }
};
</script>

<style scoped>
.home-container {
  height: 100vh;
  position: relative;
  overflow: hidden;
  background: #111;
  margin-top: -80px;
  padding-top: 80px;
}

.photo-wall {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.photo-item {
  position: absolute;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  animation: float 8s ease-in-out infinite;
  transition: transform 0.3s ease;
}

.photo-item:hover {
  transform: scale(1.1) rotate(5deg);
  z-index: 10;
}

.photo-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0) rotate(0);
  }
  50% {
    transform: translateY(-10px) rotate(2deg);
  }
}

.content {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 100%;
  text-align: center;
  padding-top: 10%;
}

.typewriter {
  font-size: 4em;
  font-weight: 600;
  color: #fff;
  text-shadow: 2px 2px 12px rgba(0, 0, 0, 0.8);
  letter-spacing: 2px;
  font-family: 'Courier New', monospace;
}

.cursor {
  animation: blink 1s infinite;
  color: #fff;
}

@keyframes blink {
  0%, 50% {
    opacity: 1;
  }
  51%, 100% {
    opacity: 0;
  }
}

/* 响应式设计 */
@media (max-width: 768px) {
  .typewriter {
    font-size: 2.5em;
  }
  
  .photo-item {
    width: 60px !important;
    height: 60px !important;
  }
}
</style>
