<template>
  <div class="card-grid-container">
    <el-row :gutter="32" justify="center">
      <el-col v-for="item in items" :key="item.title" :xs="24" :sm="12" :md="8" :lg="8" :xl="6">
        <el-card class="portfolio-card" shadow="hover">
          <img :src="item.img" alt="" class="portfolio-img" @click="previewImage(item.img)" />
          <div class="portfolio-info">
            <div class="portfolio-title">{{ item.title }}</div>
            <div class="portfolio-desc">{{ item.desc }}</div>
          </div>
        </el-card>
      </el-col>
    </el-row>
    
    <!-- 图片预览对话框 -->
    <el-dialog :visible.sync="previewVisible"  height="80%" top="3vh" custom-class="image-preview-dialog">
      <template #header>
        <div class="dialog-header">
          <span>图片预览</span>
          <el-button  icon="el-icon-close" circle @click="previewVisible = false" class="close-btn"></el-button>
        </div>
      </template>
      <img :src="previewImageUrl" alt="Preview" class="preview-image" />
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    items: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      previewVisible: false,
      previewImageUrl: ''
    };
  },
  methods: {
    previewImage(imgUrl) {
      this.previewImageUrl = imgUrl;
      this.previewVisible = true;
    }
  }
};
</script>

<style scoped>
.card-grid-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.portfolio-card {
  border-radius: 16px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.08);
  overflow: hidden;
  padding: 0;
  transition: box-shadow 0.2s;
  background: #fff;
  margin-bottom: 32px;
}
.portfolio-card:hover {
  box-shadow: 0 8px 32px rgba(0,0,0,0.16);
}
.portfolio-img {
  width: 100%;
  height: 180px;
  object-fit: cover;
  border-radius: 12px;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.portfolio-img:hover {
  transform: scale(1.02);
  box-shadow: 0 8px 32px rgba(0,0,0,0.2);
}
.portfolio-info {
  padding: 18px 20px 10px 20px;
  display: flex;
  flex-direction: column;
  gap: 6px;
}
.portfolio-title {
  font-size: 1.1rem;
  font-weight: 600;
  color: #222;
  margin-bottom: 2px;
}
.portfolio-desc {
  font-size: 0.95rem;
  color: #888;
}

/* 图片预览对话框样式 */
.image-preview-dialog {
  background: rgba(0, 0, 0, 0.9) !important;
}

.image-preview-dialog .el-dialog__header {
  padding: 16px;
  background: rgba(0, 0, 0, 0.9);
}

.dialog-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  color: #fff;
  font-size: 30px;
  font-weight: 600;
  height: 80vh;
}

.close-btn {
  font-size: 100px;
  width: 80px;
  height: 80px;
}

.image-preview-dialog .el-dialog__body {
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.9);
  min-height: 500px;
  height: 70vh;
}

.preview-image {
  max-width: 95%;
  max-height: 100vh;
  border-radius: 12px;
  object-fit: contain;
  display: block;
  margin: 0 auto;
}
</style>

